.form .inputField {
  margin-left: 5%;
  margin-top: 10px;
  width: 200px;
  height: 30px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #808080;
}
.form .inputField::-moz-placeholder {
  font-weight: 500;
}
.form .inputField::placeholder {
  font-weight: 500;
}
.form .inputField,
.form .inputField textarea {
  padding-left: 10px;
  color: rgba(0, 0, 0, 0.9);
}
.form .inputField:focus {
  outline: none;
  border: 1px solid #daa520;
}

.list {
  list-style: none;
  padding: 0;
  margin: 10px 3%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}

.scrollToTopButton {
  position: fixed;
  bottom: 20px;
  right: -5px;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}/*# sourceMappingURL=MoviesPage.module.css.map */